<template>
  <div class="about">
    <div class="full-width business-banner" :style="{backgroundImage:`url(${img})`}">
    </div>
    <div class="container crumbs">
         >>  {{tab[tabActive]}}
    </div>
    <div class="full-width tab">
      <div class="container">
        <div class="row" style="width: 600px">
          <div class="row-item" v-for="(i,n) in tab">
            <a @click="tabActive=n" :class="{active:tabActive===n}">{{i}}</a>
          </div>
        </div>
      </div>
    </div>
    <transition-group name="fade" mode="out-in">
      <div class="full-width company-des" key="0" v-show="tabActive === 0">
        <div class="container">
          <div class="le">
            <div class="cont">
              <p></p>
              <P>O2O53</P>
              <P>25</P>
            </div>
            <div class="le-row">
              <img style="margin-right: 20px" src="/static/about/aboutUs-company-1.png" alt="">
              <img src="/static/about/aboutUs-company-2.png" alt="">
            </div>
          </div>
          <div class="ri">
            <img src="/static/about/aboutUs-company-3.png" alt="">
          </div>
        </div>
      </div>
      <div class="full-width  company-cha" key="1" v-show="tabActive === 1">
        <div class="container">
          <img src="/static/about/aboutus.jpg" alt="">
        </div>
      </div>
      <div class="full-width company-len" key="2" v-show="tabActive === 2">
        <div class="container">
          <div class="detail-box">
            <div class="ph-item" v-for="i in info">
              <img :src="i.ico" alt="">
              <p class="name">{{i.ti}}</p>
              <p>{{i.ph}}</p>
            </div>
          </div>
          <div class="qc-code">
            <img src="/static/about/aboutUs-ewm.png" alt="">
            <p></p>
          </div>
        </div>
      </div>
    </transition-group>
  </div>
</template>

<script>

    export default {
        name: "about",
        data(){
          return{
            tabActive:0,
            img:'/static/detail/3.png',
            tab:['','',''],
            info:[
              {ti:'',ph:'400-678-8616',ico:'/static/about/aboutUs-contactUs-icon-phone.png'},
              {ti:'',ph:'wanggui@officemate.cn',ico:'/static/about/aboutUs-contactUs-icon-cooperation.png'},
              {ti:'',ph:'wanggui@officemate.cn',ico:'/static/about/aboutUs-contactUs-icon-jion.png'},
              {ti:'',ph:'333213A',ico:'/static/about/aboutUs-contactUs-icon-address.png'}
            ]
          }
        },
        created(){
          let data = {
            top:true,
            nav:true,
            foot:true
          };
          this.$store.dispatch('layout/setLayout', data);
          this.axios.post('http://mapi.fufentong.cn/www/api/index/getIndexGoods').then(res=>console.log(res))
        }
    }
</script>

<style scoped lang="stylus">
  .full-width.business-banner
    height 600px
    background center no-repeat
    background-size cover
    margin-top 1px
  .container.crumbs
    margin-top 50px
    color black
  .full-width.tab{
     margin-top: 48px;
     height: 40px;
     border-bottom: #ccc 1px solid;
  }
  .about .row{
    margin: 0 auto;
    position: relative;
  }
  .row-item{
    float: left;
    height: 40px;
    width 33.3333333%;
    text-align: center;
    font-size: 18px;
  }
  .row-item a{
    padding: 0 4px 20px;
    height: 40px;
    width 100px
    display: block;
    margin 0 auto
    font-weight: bold;
  }
  .row-item a.active{
    color: #038aeb;
    border-bottom: 3px solid #038aeb;
  }
  .company-cha
    .container
      padding 89px 10px 200px
  .company-des
    background-color #f2f2f2
    padding 98px 10px 200px
    &:after
      display block
      content ''
      clear both
    .le
      width 580px
      margin-right 20px
      float left
      .cont
        text-indent 2em
        line-height 29px
        font-size 16px
        padding 0 5px
        vertical-align top
      .le-row
        margin-top 22px
        img
          display block
          float left
    .ri
      width 580px
      float left
  .company-len
    .container
      padding 119px 10px 150px
      .detail-box
        position relative
        width 700px
        margin-left 80px
        &:after
          display block
          content ''
          clear both
        .ph-item
          display block
          position relative
          text-align center
          width 280px
          height 100px
          float left
          margin-right 70px
          margin-bottom 54px
          border solid 1px #038aeb
          border-radius 10px
          img
            position absolute
            top -30px
            left 111px
          p.name
            font-size 16px
            margin-top 40px
            margin-bottom 7px
            color black
      .qc-code
        position: absolute;
        right: 50px;
        top: 150px;
        p
          text-align center
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to{
    opacity: 0;
  }
</style>



