<template>
    <div id="account">
      <div class="tab">
        <ul>
          <li :class="{active:name === 'info'}"><a @click="emit('info')"></a></li>
          <li :class="{active:name === 'safe'}"><a @click="emit('safe')"></a></li>
          <li :class="{active:name === 'password'}"><a @click="emit('password')"></a></li>
        </ul>
      </div>
      <div class="main-content">
        <div class="info" v-if="name === 'info'">
          <form
          >
            <table cellspacing="0" cellpadding="0" width="600">
              <tr v-for="(i,n) in info">
                <td class="taile">{{i.name}}</td>
                <td class="input">
                  <div class="avatar" v-if="n === 0">
                    <img src="" alt="">
                    <a class="btn"></a>
                  </div>
                  <input type="text" :placeholder="i.placeholder" v-if="n!==0">
                </td>
              </tr>
            </table>
          </form>
          <div class="commit">
            <a  class="btn"></a>
          </div>
        </div>
        <div class="safe" v-if="name === 'safe'">
          <div class="safe-form">
            <div class="tr" v-for="(i,n) in safe">
              <div class="td-tile">{{i.item}}</div>
              <div class="td-cont">
                <p>{{i.cont}}</p>
                  <a v-for="j in i.action" class="action" :class="{unact:j.type === 1}" >{{j.type | act}}</a>
              </div>
            </div>
          </div>
        </div>
        <div class="password" v-if="name==='password'">
          <table cellspacing="0" cellpadding="0" width="600">
            <tr v-for="i in password">
              <td class="tile">{{i.name}}</td>
              <td class="input">
                <input type="text" :placeholder="i.placeholder">
              </td>
            </tr>
            <tr>
              <td class="tile"></td>
              <td class="input">
                <a class="btn"></a>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "account",
        props:{name},
        methods:{
          emit(a){
            this.bus.$emit('tab',a)
          }
        },
      filters:{
          act(value){
            switch (value){
              case 0:
                return ''
              case 1:
                return ''
              case 2:
                return ''
            }
          }
      },
        data(){
          return{
            info:[
              {name:'',placeholder:''},
              {name:'',placeholder:''},
              {name:'',placeholder:''},
              {name:'',placeholder:''},
              {name:'',placeholder:''},
              {name:'',placeholder:''},
              {name:'',placeholder:''},
              {name:'',placeholder:''},
            ],
            safe:[
              //action type
              //,0  ,1 ,2 

              {item:'',cont:'',action:[
                  {type:0,act:''}
                ]},
              {item:'',cont:'18523840771',action:[
                  {type:0,act:''},
                  {type:1,act:''}
                ]},
              {item:'',cont:'18523840771@163.com',action:[
                  {type:0,act:''},
                  {type:1,act:''}
                ]},
              {item:'',cont:'',action:[
                  {type:2,act:''}
                ]}
            ],
            password:[
              {name:'',placeholder:''},
              {name:'',placeholder:'6-20'},
              {name:'',placeholder:''}
            ]
          }
        }
    }
</script>

<style scoped lang="stylus">
 @require "~@/style/account.styl"
</style>



